import FirstImg from "../../assets/images/article-carousel-item1.jpg";
import SecondImg from "../../assets/images/article-carousel-item2.jpg";
import ThirdImg from "../../assets/images/article-carousel-item3.jpg";
import "../../assets/scss/fourthScreen/articles.scss";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper";
import React from "react";

//     ,       :
//   [    ] => "./swiper.min.css":  [   ] => "./swiper.min.css",
//  [    ] =>  "./swiper-bundle.min.css": [   ] => "./swiper-bundle.min.css",

const data = {
  imgs: {
    img1: FirstImg,
    img2: FirstImg,
    img3: SecondImg,
    img4: ThirdImg,
    img5: FirstImg,
    img6: FirstImg,
    img7: SecondImg,
    img8: ThirdImg,
    img9: FirstImg,
    img10: FirstImg,
    img11: SecondImg,
    img12: ThirdImg,
  },
  dates: {
    date1: "12  2021",
    date2: "12  2021",
    date3: "12  2021",
    date4: "12  2021",
    date5: "12  2021",
    date6: "12  2021",
    date7: "12  2021",
    date8: "12  2021",
    date9: "12  2021",
    date10: "12  2021",
    date11: "12  2021",
    date12: "12  2021",
  },
  titles: {
    title1: "      ",
    title2: "       COVID-19",
    title3:
      "     , - ",
    title4: "     ",
    title5: "      ",
    title6: "       COVID-19",
    title7:
      "     , - ",
    title8: "     ",
    title9: "      ",
    title10: "       COVID-19",
    title11:
      "     , - ",
    title12: "     ",
  },
  subtitles: {
    subtitle1:
      "           18 .",
    subtitle2: "       COVID-19",
    subtitle3:
      "        - - , ...",
    subtitle4: "     .",
    subtitle5:
      "           18 .",
    subtitle6: "       COVID-19",
    subtitle7:
      "        - - , ...",
    subtitle8: "     .",
    subtitle9:
      "           18 .",
    subtitle10: "       COVID-19",
    subtitle11:
      "        - - , ...",
    subtitle12: "     .",
  },
};

let img = Object.values(data.imgs);
let date = Object.values(data.dates);
let title = Object.values(data.titles);
let subtitle = Object.values(data.subtitles);

const SlideElem = ({ img, date, title, subtitle }) => {
  return (
    <a href="/" className="carousel-article">
      <div className="article-block">
        <img
          className="desktop-banner"
          src={img}
          alt="article_banner_desktop"
        />
        <div className="text-block">
          <p className="article-date">{date}</p>
          <p className="article-title">{title}</p>
          <p className="article-subtitle">{subtitle}</p>
        </div>
      </div>
    </a>
  );
};

const slides = [];
for (let i = 0; i < 6; i += 1) {
  slides.push(
    <SwiperSlide key={`slide-${i}`}>
      <SlideElem
        key={i}
        img={img[i]}
        date={date[i]}
        title={title[i]}
        subtitle={subtitle[i]}
      />
    </SwiperSlide>
  );
}

const ArticleCarouselElem = () => {
  return (
    <React.Fragment>
      <Swiper
        modules={[Navigation, Pagination]}
        id="main"
        spaceBetween={16}
        slidesPerView={"auto"}
        navigation
        pagination={{ clickable: true }}
        loop={true}
      >
        {slides}
      </Swiper>
      <a className="show-all-articles__link" href="/">
        <button className="show-all-articles">  </button>
      </a>
    </React.Fragment>
  );
};

export default ArticleCarouselElem;
