import { useEffect } from "react";
import "../../assets/scss/secondScreen/popular.scss";
import PopularMobile from "./Popular-mobile";

const amount = 12;
const data = {
  title: {
    title: "   () + ",
    title1: " D",
    title2: "   () + ",
    title3: "     Covid-19",
    title4: "  (13 )",
    title5: "  (13 )",
    title6: " ",
    title7: " ",
    title8: "  ",
    title9: "  ",
    title10: " ",
    title11: "  ",
    title12: "  ",
  },
  price: {
    price: 1450,
    price1: 1450,
    price3: 1450,
    price4: 1450,
    price5: 1450,
    price6: 1450,
    price7: 1450,
    price8: 1450,
    price9: 1450,
    price10: 1450,
    price12: 1450,
    price13: 1450,
  },
  dottedPrice: {
    dottedPrice: 1450,
    dottedPrice1: 1450,
    dottedPrice3: 1450,
    dottedPrice4: 1450,
    dottedPrice5: 1450,
    dottedPrice6: 1450,
    dottedPrice7: 1450,
    dottedPrice8: 1450,
    dottedPrice9: 1450,
    dottedPrice10: 1450,
    dottedPrice12: 1450,
    dottedPrice13: 1450,
  },
  hoverSubtitle: {
    hoverSubtitle:
      "    IgM  IgG    TORCH-",
    hoverSubtitle1:
      "    IgM  IgG    TORCH-",
    hoverSubtitle2:
      "    IgM  IgG    TORCH-",
    hoverSubtitle3:
      "    IgM  IgG    TORCH-",
    hoverSubtitle4:
      "    IgM  IgG    TORCH-",
    hoverSubtitle5:
      "    IgM  IgG    TORCH-",
    hoverSubtitle6:
      "    IgM  IgG    TORCH-",
    hoverSubtitle7:
      "    IgM  IgG    TORCH-",
    hoverSubtitle8:
      "    IgM  IgG    TORCH-",
    hoverSubtitle9:
      "    IgM  IgG    TORCH-",
    hoverSubtitle10:
      "    IgM  IgG    TORCH-",
    hoverSubtitle11:
      "    IgM  IgG    TORCH-",
  },
};

const PopularElem = ({ id, title, dottedPrice, price, hoverSubtitle }) => {
  return (
    <a href="/" className="popular-services-item" key={id}>
      <div className="block-wrapper"></div>
      <h3 className="popular-services-item-text">{title}</h3>
      <div className="popular-services-item-hovered">
        <p className="dotted-price">
          <del>
            {dottedPrice} <span className="rub">i</span>
          </del>
        </p>
        <p className="price">
          {price} <span className="rub">i</span>
        </p>
        <h3 className="popular-services-item-hovered-title">{title}</h3>
        <p className="popular-services-item-hovered-subtitle">
          {hoverSubtitle}
        </p>
      </div>
    </a>
  );
};

const amountList = [...Array(amount).keys()].map((n) => n.toString());
let titles = Object.values(data.title);
let prices = Object.values(data.price);
let dottedPrices = Object.values(data.price);
let hoverSubtitles = Object.values(data.hoverSubtitle);

const Popular = () => {
  useEffect(() => {
    (function () {
      const cropElement = document.querySelectorAll(
        "p.popular-services-item-hovered-subtitle"
      ); //  

      let size = 60;
      let endCharacter = "..."; // 

      cropElement.forEach((el) => {
        let text = el.innerHTML;

        if (el.innerHTML.length > size) {
          text = text.substr(0, size);
          el.innerHTML = text + endCharacter;
        }
      });
    })();
  }, []);

  return (
    <>
      <div className="popular">
        <div className="popular-header">
          <h1 className="popular-header__title"> </h1>
          <div className="categories">
            <a className="popular-header__category-link" href="/">
               
            </a>
          </div>
        </div>

        <div className="popular-services">
          {amountList.map((id) => (
            <PopularElem
              key={id}
              id={id}
              title={titles[id]}
              price={prices[id]}
              dottedPrice={dottedPrices[id]}
              hoverSubtitle={hoverSubtitles[id]}
            />
          ))}
        </div>
      </div>
      <PopularMobile />
    </>
  );
};

export default Popular;
